import { Body, Controller, Param, Patch } from '@nestjs/common';
import {
  ApiBadRequestResponse,
  ApiConsumes,
  ApiInternalServerErrorResponse,
  ApiNotFoundResponse,
  ApiOkResponse,
  ApiOperation,
  ApiTags,
  ApiUnauthorizedResponse,
} from '@nestjs/swagger';

import { ProjectScenario } from '../../../application/scenario/project';
import { CustomLogger } from '../../../utility';
import { ProjectUpdateRequest } from '../request';
import { ProjectResponse } from '../response';

@ApiTags('Projects')
@Controller('projects')
export class ProjectUpdateController {
  constructor(
    readonly logger: CustomLogger,
    readonly scenario: ProjectScenario,
  ) {
    this.logger.setContext(ProjectUpdateController.name);
  }
  @Patch(':projectId')
  @ApiConsumes('application/json')
  @ApiOperation({
    description: 'Update Project',
  })
  @ApiOkResponse({ type: ProjectResponse })
  @ApiNotFoundResponse({
    description: '案件が見つからない',
  })
  @ApiBadRequestResponse({
    description: 'リクエスト内容が不正',
  })
  @ApiInternalServerErrorResponse({
    description: 'サーバーエラー',
  })
  @ApiUnauthorizedResponse({
    description: '認証エラー',
  })
  async update(
    @Param('projectId') projectId: string,
    @Body() body: ProjectUpdateRequest,
  ): Promise<ProjectResponse> {
    return await this.scenario.updateProject(projectId, body);
  }
}
